import "./Card.scss";

// 
// mode == img-library border
// title ,title
const Card = ({ image, title, subTitle, subTitleClass, mode, onClick, style }) => {

    const dispatchMode = ()=>{
        let imageStye = {}
        switch(mode){
            // 
            case "img-library":
                imageStye = {
                    padding: "5px",
                    border: "1px solid gray"
                }
                break;
            //
            default:
                imageStye = {

                }
                break;
        }
        return imageStye
    }

    return (
        <div className="card-container" onClick={onClick} style={style}>
            <div>
                <img style={dispatchMode()} src={image} alt="" />
            </div>
            <div className="card-content" style={{ "display": title ? "flex" : "none" }}>
                <h2>{title}</h2>
                <h3 className={subTitleClass ? subTitleClass : ""} style={{ "display": subTitle ? "block" : "none" }}>{subTitle}</h3>
            </div>
        </div>
    )
}

export default Card;